<template>
  <div class="container">
    <div class="main">
      <NewsBody v-if="loaded" :news="this.thread" />
    </div>

    <div class="main" v-for="(comment, key) in this.comments" :key="key">
      <div class="comment-author">
        <img :src="`/img/avatars/${comment.avatar}`" />
        <p style="margin-left:10px">
          <span class="comment-username" :class="comment.rank">{{ comment.username }}</span>
          <br>
          <span class="comment-rank">{{ comment.rank }}</span>
        </p>

        <p class="comment-date">
          {{date(new Date(comment.created_at)) | moment("from", "now")}}
        </p>
      </div>
      <div class="comment-text" v-html="comment.content"></div>

      <footer class="comment-options">
        <button>edit</button>
        <button>delete</button>
        <button @click="quote(comment.content)">reply</button>
      </footer>
    </div>

    <div class="main">
      <TextEditor @content-captured="postComment" :content="''" />
    </div>
  </div>
</template>

<script>
import components from "./script/components";
import computed from "./script/computed";
import methods from "./script/methods";
import watchers from "./script/watchers";

export default {
  data() {
    return {
      comments: [],
      thread: {},
      loaded: false,
    };
  },
  components,
  computed,
  methods,
  watchers,
  async created() {
    const id = this.$route.params.id;
    const siteArea = 0;

    try {
      const doc = await this.$http.get(`/news/${id}/${siteArea}`);
      this.comments = doc.data.records.comments;
      delete doc.data.records.comments;
      this.thread = { ...doc.data.records };
      this.loaded = true;
    } catch (err) {
      alert(err);
    }
  },
};
</script>

